  const cleaning = ['//www.bigbasket.com/media/customPage/77880b23-0233-4fad-b54a-a93c998e0d20/699e0aef-4008-498a-8687-de63e7146998/babf9e60-b997-407c-a134-08540639202d/hp_c&h_m_detergents-and-fabric-care_480_250623.jpg', '//www.bigbasket.com/media/customPage/77880b23-0233-4fad-b54a-a93c998e0d20/699e0aef-4008-498a-8687-de63e7146998/babf9e60-b997-407c-a134-08540639202d/hp_c&h_m_fresheners-repellents_480_250623.jpg', '//www.bigbasket.com/media/customPage/77880b23-0233-4fad-b54a-a93c998e0d20/699e0aef-4008-498a-8687-de63e7146998/babf9e60-b997-407c-a134-08540639202d/hp_c&h_m_cleaners-disinfectants_480_250623.jpg', '//www.bigbasket.com/media/customPage/77880b23-0233-4fad-b54a-a93c998e0d20/699e0aef-4008-498a-8687-de63e7146998/babf9e60-b997-407c-a134-08540639202d/hp_c&h_m_disposables-garbage-bags_480_250623.jpg'];
