import React, { useState } from 'react'
import './Login.css'
import BbIcon from '../assets/images/bb-icon.png';
import { useDispatch, useSelector } from "react-redux";
import { login } from '../Redux/Slice';
import { useNavigate } from 'react-router-dom';


const Login = () => {

  const state = useSelector(({ data }) => data);
  console.log('login', state);

  const dispatch = useDispatch();
  const navigate = useNavigate();
  const [isSubmitted, setSubmit] = useState(false);
  const [data, setDeta] = useState({
    email: '',
    password: '',
  });


  const handle = (event) => {

    if (event.target.name === "email") {
      setDeta({ ...data, email: event.target.value });
    }
    if (event.target.name === "password") {
      setDeta({ ...data, password: event.target.value });
    }

  };

  const handleSubmit = (e) => {
    e.preventDefault();
    setSubmit(true);
    let array = JSON.parse(localStorage.getItem('users'));
    console.log(array);
    if (data.email === array.email && data.password === array.password) {
      dispatch(login(true));
      alert('Successfully Login');
      navigate('/');
    } else {
      dispatch(login(false));
      alert('Email & Password Doesnot Match');
    }
  }

  const goToSignup = () => {
    navigate('/signup')
  }


  return (
    <div className="bg-img">

      <div className="container">
        <div className="row">
          <div className="col-12 col-sm-6 col-lg-6">
            <form>
              <fieldset>

                <legend>
                  <img src={BbIcon} alt={"img"} />
                  <span className="name">BigBasket</span>
                </legend>

                <div className="position">
                  <input type="text" value={data.email} name="email" id="email" placeholder="Enter a Email Address OR Phone Number" onChange={handle} />
                  {data.email === "" && isSubmitted && <div>Email is Required<i style={{ display: 'block' }} className="bi bi-exclamation-circle-fill error"></i></div>}
                </div>

                <div className="position">
                  <input type="password" value={data.password} name="password" id="password" placeholder="Enter a Password" onChange={handle} />
                  {data.password === "" && isSubmitted && <div>Password is Required<i style={{ display: 'block' }} className="bi bi-exclamation-circle-fill error"></i></div>}
                </div>

                <div className="button">
                  <button className="blue-color" id="login1" onClick={handleSubmit}><a href={"#raji"} className="login">Log in</a></button>
                  <a href={"#raji"} className="forgot hover">Forgotten password?</a>
                </div>

                <div className="g-btn">
                  <button className="green-color" onClick={goToSignup}>
                    <a href={"#raji"}>Create new account</a>
                  </button>
                </div>

              </fieldset>
            </form>
          </div>
        </div>
      </div>
    </div>
  )
}

export default Login