import React, { useState, useEffect } from 'react';
import Button from '@mui/material/Button';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogTitle from '@mui/material/DialogTitle';
import useMediaQuery from '@mui/material/useMediaQuery';
import { useTheme } from '@mui/material/styles';
import { useNavigate } from "react-router-dom";
// import { Typography } from '@mui/material';
import { useDispatch, useSelector } from "react-redux";
import { clearCard, login } from "../Redux/Slice";
import Lottie from "lottie-react";
import SuccessAnim from "../Success.json";
import './Success.css'
import PaidIcon from '@mui/icons-material/Paid';


const Success = () => {
    const state = useSelector(({ data }) => data);
    console.log('success', state);
    const dispatch = useDispatch();
    const [count, setCount] = useState(9);
    const navigate = useNavigate();

    useEffect(() => {
        setInterval(() => setCount((count) => count - 1), 1000);
        setTimeout(() => {
            navigate("/login");
            dispatch(login(false));
            window.location.reload(false); //refresh the page
        }, 9000);
    }, [navigate]);

    const [totalPrice, updateTotalPrice] = useState(0);

    useEffect(() => {
        if (state.addToCart.length) {
            const total = state.addToCart.reduce((total, value) => {
                total += value.quantity * value.discountedPrice
                return total;
            }, 0)
            updateTotalPrice(total)
        }
    }, [state.addToCart])

    const [open, setOpen] = useState(false);
    const theme = useTheme();
    const fullScreen = useMediaQuery(theme.breakpoints.down('md'));

    const handleClickOpen = () => {
        setOpen(true);
        dispatch(clearCard());
    };

    const handleClose = () => {
        setOpen(false);
    };

    return (
        <div className="anim_success">
            <div className='customer-details'>
                <h2>YOUR PAYMENT IS : Rs $ {totalPrice} </h2>
            </div>
            <Button variant="contained" className='btnnn' color="success" startIcon={<PaidIcon />} onClick={() => handleClickOpen()}>
                Payment Process
            </Button>
            <Dialog
                fullScreen={fullScreen}
                open={open}
                onClose={handleClose}
                aria-labelledby="responsive-dialog-title"
            >
                <DialogTitle id="responsive-dialog-title">
                    {"Payment Successfully"}
                    <Lottie
                        animationData={SuccessAnim}
                        loop={false}
                    />
                </DialogTitle>

                <div>
                    <p className='paraaa'>Your Order have been placed successfully. You will be redirected in {""}{count} seconds</p>
                    <p className='paraaa'>Thankyou!!!😀🤩</p>
                </div>

                <DialogActions>
                    <Button autoFocus variant="outlined" color="error" onClick={() => handleClose()}>
                        Close
                    </Button>
                </DialogActions>
            </Dialog>
        </div>
    );
}

export default Success;


