import React from "react";
import Header from "./Header/Header";
import Footer from "./Footer/Footer";
import Home from "./Home/Home";
import Details from "./Details/Details";
import AllBasket from "./AllBasket/AllBasket";
import SmallBasket from './SmallBasket/SmallBasket'
import { BrowserRouter, Routes, Route, Navigate } from "react-router-dom";
import { Provider, useSelector } from "react-redux";
import { stores } from "./Redux/Store";
import Product from "./Product/Product";
import Success from "./PaymentSuccess/Success";
import Login from "./Login/Login";
import SignUp from "./SignUp/SignUp";
import FeedBack from "./FeedBack/FeedBack";

const Redux = () => {
    return (
        <>
            <Provider store={stores}>
                <Routing />
            </Provider>
        </>
    );
}

const Main = () => {
    return (
        <>
            <Home />
            <SmallBasket />
            <Product />
        </>
    );
}


const Routing = () => {

    const state = useSelector(({ data }) => data);
    console.log('router', state);
    return (
        <>
            {
                state.isLogin ? (
                    <BrowserRouter>
                        <Header />
                        <Routes>
                            <Route path='/' element={<Main />} />
                            <Route path='/details/:key/:index' element={<Details />} />
                            <Route path='/allBaskets/:key/:heading' element={<AllBasket />} />
                            <Route path="/feedback" element={<FeedBack />}></Route>
                            <Route path='/paymentsuccessful' element={<Success />}></Route>
                        </Routes>
                        <Footer />
                    </BrowserRouter>
                ) : (
                    <BrowserRouter>
                        <Routes>
                            <Route path="/login" element={<Login />}></Route>
                            <Route path="/signup" element={<SignUp />}></Route>
                            <Route path='*' element={<Navigate to={'/login'} />} />
                        </Routes>
                    </BrowserRouter>
                )
            }
        </>
    );
}

export default Redux;

