import React, { useEffect } from "react";
import "./FeedBack.css";
import { useState } from "react";
import Button from "@mui/material/Button";
import { useNavigate } from "react-router-dom";
import { useSelector } from "react-redux";

const FeedBack = () => {
    const state = useSelector(({ data }) => data);
    console.log('feedback', state);
    const navigate = useNavigate();

    const [dates, setDate] = useState("");
    const [names, setName] = useState("");
    const [mails, setMail] = useState("");
    const [mobiles, setMobile] = useState("");
    const [feeds, setFeeds] = useState("");
    const [addresss, setAddess] = useState('');
    const [open, setOpen] = useState(false);

    const handleClickOpen = (e) => {
        // e.preventDefault();
        alert('Order Details Completed');
        navigate('/paymentsuccessful')
        setOpen(true);
    };

    const handle = (event) => {
        if (event.target.name === "ename") {
            setName(event.target.value);
        }
        if (event.target.name === "email") {
            setMail(event.target.value);
        }
        if (event.target.name === "ephone") {
            setMobile(event.target.value);
        }
        if (event.target.name === "eaddress") {
            setAddess(event.target.value);
        }
        if (event.target.name === "edate") {
            setDate(event.target.value);
        }
        if (event.target.name === "efeedback") {
            setFeeds(event.target.value);
        }
    }


    const [totalPrice, updateTotalPrice] = useState(0);

    useEffect(() => {
        if (state.addToCart.length) {
            const total = state.addToCart.reduce((total, value) => {
                total += value.quantity * value.discountedPrice

                return total;
            }, 0)
            updateTotalPrice(total)
        }
    }, [state.addToCart])

    return (
        <div className="form-container">
            <div className="feeddiv">
                <h2 className="head">Submit Your Order Details Payment Processing</h2>
                <>
                    <form className="form">
                        <div>
                            <label name="edate" className="text">
                                Date:{" "}
                            </label>
                            <input type="date" value={dates} name="edate" className="input" onChange={handle}></input>
                        </div>
                        <div>
                            <label name="ename" className="text">
                                Name:{" "}
                            </label>
                            <input type="text" value={names} name="ename" className="input" onChange={handle}></input>
                        </div>
                        <div>
                            <label name="email" className="text">
                                Email:{" "}
                            </label>
                            <input type="email" value={mails} name="email" className="input" onChange={handle}></input>
                        </div>
                        <div>
                            <label name="ephone" className="text">
                                Phone:{" "}
                            </label>
                            <input
                                type="number"
                                className="input"
                                max="==10"
                                onChange={handle}
                                name="ephone"
                                value={mobiles}
                            ></input>
                        </div>
                        <div>
                            <label name="eaddress" className="text">
                                Address:{" "}
                            </label>
                            <input
                                type="text"
                                className="input"
                                name="eaddress"
                                value={addresss}
                                onChange={handle}
                            ></input>
                        </div>
                        <div>
                            <label name="efeedback" className="text">
                                Feedback:{" "}
                            </label>
                            <input type="text" value={feeds} name="efeedback" className="input" onChange={handle}></input>
                        </div>
                        <div>
                            <label name="pay" className="text">
                                Payment:{" "}
                            </label>
                            <span className="input span">Rs. {totalPrice}</span>
                        </div>
                        <div>
                            <Button
                                type="button"
                                variant="contained"
                                className="btn"
                                onClick={() => handleClickOpen()}
                            >
                                Submit
                            </Button>
                        </div>
                    </form>
                </>
            </div>
        </div>
    );
}

export default FeedBack;


