import React, { useState } from 'react'
import BbIcon from '../assets/images/bb-icon.png';
import './SignUp.css';
import { useSelector } from "react-redux";
import { useNavigate } from 'react-router-dom';
// import { login } from '../Redux/Slice';



const SignUp = () => {

  const state = useSelector(({ data }) => data);
  console.log('signup', state);
  const navigate = useNavigate()
  // const dispatch = useDispatch();

  const [isSubmitted, setSubmit] = useState(false);
  const [data, setDeta] = useState({
    userName: '',
    email: '',
    password: '',
    conformPassword: '',
  });

  const handle = (event) => {

    if (event.target.name === "userName") {
      setDeta({ ...data, userName: event.target.value });
    }
    if (event.target.name === "email") {
      setDeta({ ...data, email: event.target.value });
    }
    if (event.target.name === "password") {
      setDeta({ ...data, password: event.target.value });
    }
    if (event.target.name === "cpassword") {
      setDeta({ ...data, conformPassword: event.target.value });
    }
  };
  // if (localStorage.getItem("users") == null) {
  //   localStorage.setItem("users", "[]");
  // }
  const handleSubmit = (e) => {
    e.preventDefault();

    // let array = JSON.parse(localStorage.getItem("users"));
    // let length = array.length;
    // console.log(length)

    let values = localStorage.setItem('users', JSON.stringify(data));
    console.log(values);
    
    setSubmit(true);

    if (data.userName !== '' && data.email !== '' && data.password !== '' && data.conformPassword !== '') {
      if (data.password === data.conformPassword) {
        setSubmit(false);
        // dispatch(login(true));
        navigate('/login');
      }
    }
  }


  return (
    <div id="home" className="bg-img">
      <div className="container">
        <div className="signup-row row">
          <div className="col-12 col-sm-6 col-lg-6">
            <form>
              <fieldset>

                <legend>
                  <img src={BbIcon} alt={"img"} /><span
                    className="name">BigBasket</span>
                </legend>

                <div className="signup-us position">
                  <input name='userName' value={data.userName} className="userName" id="userName" type="text" placeholder="Enter a UserName" onChange={handle} />
                  {isSubmitted ? (data.userName === "" ? <div>UserName is Required<i style={{ display: 'block' }} className="bi bi-exclamation-circle-fill error"></i></div> : null) : null}
                  {/* {emptyError.userName ? (data.userName==='' ? <p><i className="bi bi-star-fill"></i> User Name cannot be empty <i className="bi bi-star-fill"></i></p>:null):null} */}
                </div>

                <div className="signup-email position">
                  <input name='email' value={data.email} className="emailid" id="emailid" type="text" placeholder="Enter a Email" onChange={handle} />
                  {isSubmitted ? (data.email === "" ? <div>Email is Required<i style={{ display: 'block' }} className="bi bi-exclamation-circle-fill error"></i></div> : null) : null}
                </div>

                <div className="signup-pw position">
                  <input name='password' value={data.password} className="password" id="password" type="password" placeholder="Enter a Create Password" onChange={handle} />
                  {isSubmitted ? (data.password === "" ? <div>Password is Required<i style={{ display: 'block' }} className="bi bi-exclamation-circle-fill error"></i></div> : null) : null}
                </div>

                <div className="signup-cpw position">
                  <input name='cpassword' value={data.conformPassword} className="c-password" id="c-password" type="password"
                    placeholder="Enter a Conform Password" onChange={handle} />
                  {isSubmitted ? (data.conformPassword === "" ? <div>ConformPassword is Required<i style={{ display: 'block' }} className="bi bi-exclamation-circle-fill error"></i></div> : null) : null}
                </div>

                <div className="btns">
                  <button className="submit" id="signup" onClick={handleSubmit}><a href={"#raji"} className="sign-up-submit">SIGN UP</a></button>
                  <p>already you have a account ?<a href={"#raji"} onClick={()=>navigate('/login')}> sign in</a> </p>
                </div>

              </fieldset>
            </form>
          </div>
        </div>
      </div>
    </div>
  )
}

export default SignUp